.process {
  display: flex;
  flex-direction: column;
  flex-wrap: wrap;
  gap: 27px;
  margin-top: -2px;

  @include vp-1199 {
    gap: 40px;
  }

  @include vp-767 {
    gap: 23px;
  }

  h3 {
    @include vp-1199 {
      font-size: 18px;
      font-weight: 700;
    }

    @include vp-767 {
      font-size: 16px;
      font-weight: 700;
    }
  }
}

.process__list {
  margin: 0;
  padding: 0;
  list-style: none;
  display: grid;
  grid-template-columns: repeat(4, 253px);
  gap: 50px 0;
  flex: 1 0 0;
  align-items: end;

  @include vp-1199 {
    max-width: 678px;
    display: flex;
    flex-direction: row;
    flex-wrap: wrap;
    gap: 71px 60px;
  }

  @include vp-767 {
    max-width: 100%;
    display: flex;
    flex-direction: column;
    flex-wrap: wrap;
    gap: 25px
  }

  p {
    margin: 0;
    width: 210px;
    padding-left: 10px;

    @include vp-1199 {
      font-size: 14px;
      line-height: 22px;
    }

    @include vp-767 {
      font-size: 12px;
      line-height: 18px;
      padding-left: 13px;
    }
  }

  svg {
    display: block;
    width: 25px;
    height: 25px;
    margin-top: 2px;

    @include vp-767 {
      width: 21px;
      height: 19px;
      margin-top: 7px;
    }
  }

  li {
    position: relative;
    display: grid;
    grid-template-columns: min-content minmax(238px, auto);
    align-items: end;
    margin-bottom: 24px;
    align-items: flex-start;
    align-self: stretch;
    padding: 0 10px;

    @media (min-width: 1200px) and (max-width: 1439px) {
      grid-template-columns: min-content minmax(222px, auto);
    }

    @include vp-1199 {
      grid-template-columns: fit-content auto;
      align-items: start;
      margin-bottom: 0;
    }

    @include vp-767 {
      display: flex;
      flex-direction: row;
      padding: 0;
    }

    &::before {
      position: absolute;
      bottom: -16px;
      left: 10px;
      width: 14px;
      height: 14px;
      background-color: $color-navy-blue;
      border-radius: 50%;
      content: "";

      @include vp-1199 {
        bottom: -24px;
      }

      @include vp-767 {
        width: 14px;
        height: 13px;
        position: relative;
        left: 0;
        top: 19px;
        transform: translateY(-50%);
        margin-right: 15px;
      }
    }

    &::after {
      content: "";
      position: absolute;
      right: 0;
      bottom: -10px;
      left: 20px;
      height: 1px;
      background-color: $color-navy-blue-20;

      @media (min-width: 1200px) {
        min-width: 100%;
      }

      // @media (min-width: 768px) and (max-width: 1199px) {
      //   display: none;
      // }

      @include vp-767 {
        max-width: 1px;
        height: 100%;
        min-height: 61px;
        left: 6px;
        top: 15px;
      }
    }

    &:last-child {
      @media (min-width: 1200px) {
        &::after {
          right: -100vw;
        }
      }

      @include vp-1199 {
        &::after {
          top: 0;
          width: 335px;
        }
      }

      @include vp-767 {
        &::after {
          top: -40px;
        }
      }
    }
  }

  li:first-child {
    &::after {
      @include vp-1199 {
        width: 335px;
        bottom: -17px;
      }
    }

    & + li  {
      @include vp-1199 {
        &::after {
          max-width: 1px;
          height: 56px;
          top: 56px;
          left: 17px;
        }
      }

      @include vp-767 {
        order: 0;

        &::after {
          top: 20px;
          left: 6px;
        }
      }
    }

    & + li + li {
      @include vp-1199 {
        order: 1;

        &::after {
          display: none;
        }
      }

      @include vp-767 {
        order: 0;

        &::after {
          display: block;
        }
      }
    }
  }

  li:last-child,
  li:first-child + li + li {
    @media (min-width: 768px) and (max-width: 1199px) {
      padding-top: 20px;
    }

    &::before {
      @media (min-width: 768px) and (max-width: 1199px) {
        top: -7px;
        bottom: unset;
      }
    }
  }
}
